import React, { useState,useCallback,useEffect} from 'react';
import './App.css';

const Enums = {
	hp: "生命值",
	defence: "防御力",
	magic: "法器",
	sword: "单手剑",
	glaive: "大剑",
	arch: "弓",
	longHandle: "长柄",
	attack: "攻击力固定值",
	atRate: "攻击力百分比",
	criticalRate: "暴击率",
	criticalStrike: "爆伤",
	physicsReward: "物理",
	fire: "火",
	electric: "雷",
	water: "水",
	ice: "冰",
	wind: "风",
	rock: "岩",
	treatment: "治疗",
	charging: "元素充能",
	reaction: "元素精通",
	flower:"花",
	plume:"羽毛",
	sandglass:"沙漏",
	cann:"酒杯",
	header:"头部",
	damageRate:"造成的伤害增加"
}

function Weapon(props){
	const [ShowCreateW, setShowCreateW] = useState(false)
	let {Weapons} = props;
	const createWeapon = () => {
		setShowCreateW(true);
	}
	const CanelcreateWeapon = () => {
		setShowCreateW(false);
	}
	return (
		<div className="weapon_container">
			{
				Weapons.map(item => {
					return (
						<div id={item.id}>
							item.name							
						</div>
					)
				})
			}
			<div className={ShowCreateW? "show" : "hide"}>
				<div onClick={CanelcreateWeapon} className="mderContainer">
					<div className="InputsBox">
						<button onClick={CanelcreateWeapon} className="button button_red righttop">取消</button>
					</div>
				</div>
			</div>
			<div onClick={createWeapon} className="newWeapon">NEW</div>
		</div>
	)
}

function App() {
	const [MenuPage, setMenuPage] = useState(getData("MenuPage") || "1")
	const [Weapons, setWeapons] = useState(JSON.parse(getData("Weapons") || "[]"))
	const menuChange = useCallback((v) => {
		setMenuPage(() => v);
		saveData({ MenuPage: v })
	},[])
	const changeWeapons = useCallback((v) => {
		setWeapons(() => v);
		saveData({ Weapons: v })
	},[])

	useEffect(() => {
		setMenuPage(()=>MenuPage)
	}, [MenuPage])
	

	return (
		<div className="App">
			<div className="head">{MenuPage}</div>
			<div className="main">
				<div className={MenuPage === "1" ? "show" : "hide"}>{Weapon({Weapons,changeWeapons})}</div>
				<div className={MenuPage === "2" ? "show" : "hide"}>2</div>
				<div className={MenuPage === "3" ? "show" : "hide"}>3</div>
				<div className={MenuPage === "4" ? "show" : "hide"}>4</div>
			</div>
			<div className="menu">
				<div onClick={() => menuChange("1")} className={MenuPage === "1" ? "menu_item menu_item_active" : "menu_item"}>1</div>
				<div onClick={() => menuChange("2")} className={MenuPage === "2" ? "menu_item menu_item_active" : "menu_item"}>2</div>
				<div onClick={() => menuChange("3")} className={MenuPage === "3" ? "menu_item menu_item_active" : "menu_item"}>3</div>
				<div onClick={() => menuChange("4")} className={MenuPage === "4" ? "menu_item menu_item_active" : "menu_item"}>4</div>
			</div>
		</div>
	);
}

function saveData(props) {
	for (let key in props) {
		let value = props[key];
		if (!/string|number/ig.test(typeof key)) {
			value = JSON.stringify(value)
		}
		window.localStorage.setItem(key, value)
	}
}

function getData(key) {
	return window.localStorage[key]
}


export default App;
